<template>
  <div class="game face-game">
    <video ref="video" muted preload @loadeddata="onVideoLoaded()" @ended="onVideoEnded()">
      <source src="./../assets/video2.mp4" type="video/mp4">
    </video>
    <div v-if="!loading&&!playing" @click="clickPlay()" class="play-btn">
      <p v-if="!complete">CLICK TO PLAY</p>
      <p v-if="complete">YOU SCORED {{score}}. PLAY AGAIN?</p>
    </div>
    <div v-if="playing" class="game-ui">
      <p class="score">SCORE: {{score.toString().padStart(3,"0")}}</p>
    </div>
    <particle-canvas :face="faceFrames[currentFrame]" :playing="playing" @pop="onPop()" />
  </div>
</template>

<script>
import ParticleCanvas from './ParticleCanvas.vue'
import faceFrames from './../assets/video.json'

export default {
  components: { ParticleCanvas },
  name: 'FaceGame',
  props: {
  },
  data() {
    return { 
      loading: false,
      playing: false,
      complete: false,
      lastUpdate: performance.now(),
      currentFrame: 0,
      faceFrames: faceFrames,
      score: 0,
      completeTime: -1000,
    }
  },
  mounted() { 
    //
    this.loading = true;
  },
  methods: {
    onVideoLoaded() { 
      this.loading = false;
    },
    clickPlay() {
      // debounce for game complete
      if(Date.now() - this.completeTime < 1500) return;
      
      this.score = 0;
      this.$refs.video.currentTime = 0;
      this.$refs.video.play();
      this.updateFaces();
      this.playing = true;
      this.complete = false;
    },
    onVideoEnded() {
      this.playing = false;
      this.complete = true;
      this.completeTime = Date.now();
    },
    updateFaces() {
      
      const frame = faceFrames[this.currentFrame];
      const timeDiff = this.$refs.video.currentTime - frame.t;

      // check if we need to move to the next frame
      if(timeDiff >= 0.15 && this.currentFrame + 1 < faceFrames.length) this.currentFrame++;

      // check if the video looped back
      if(timeDiff < -1) this.currentFrame = 0;
      
      // keep updating
      window.requestAnimationFrame(this.updateFaces) 
    },
    onPop() {
      this.score += 100;
    }
  }
}
</script>

