<template>
  <div class="container" ref="container">
    <canvas ref="trailCanvas" class="trail-canvas"/>
    <canvas ref="canvas" @mousedown="tapCheck($event)" />
    <audio ref="popSound" src="../assets/pop.mp3" preload controls="none" style="display:none" />
  </div>
</template>

<script>
import Proton from 'proton-engine'
import emoji from '../assets/emoji'
//import NFQDomRenderer from '../js/NFQDomRenderer'

export default {
  name: 'ParticleCanvas',
  props: {
    face:Object,
    playing:Boolean
  },
  emits: ['pop'],
  data() {
    return { 
      proton: null,
      eye1: null,
      eye2: null,
      eye1bg: null,
      eye2bg: null,
      mouth: null,
      popEmitter: null,
      rate: null,
      scale: 1
    }
  },
  async mounted() { 
    console.log("particles mounted")
    this.resizeCanvasToDisplaySize()
    //const container = this.$refs.container;

    const proton = this.proton = new Proton();
    const protonTrail = this.protonTrail = new Proton();

    
    const eye1 = this.eye1 = new Proton.Emitter();
    const eye2 = this.eye2 = new Proton.Emitter();
    const eye1bg = this.eye1bg = new Proton.Emitter();
    const eye2bg = this.eye2bg = new Proton.Emitter();

    this.rate1 = new Proton.Rate(1, Proton.getSpan(0.5, 1));
    this.rate1bg = new Proton.Rate(4, Proton.getSpan(0.1, 0.2));
    this.rate2 = new Proton.Rate(1, Proton.getSpan(0.5, 1));
    this.rate2bg = new Proton.Rate(4, Proton.getSpan(0.1, 0.2));

    let rad = new Proton.Radius(30 * this.scale)
    let radbg = new Proton.Radius(4 * this.scale)
    let life = new Proton.Life(2)
    let lifeBg = new Proton.Life(1)
    let vel = new Proton.Velocity(Proton.getSpan(2 * this.scale,4 * this.scale), Proton.getSpan(-40, 40), 'polar')
    let velbg = new Proton.Velocity(Proton.getSpan(2 * this.scale,6 * this.scale), Proton.getSpan(-40, 40), 'polar')
    let body = new Proton.Body(emoji);

    //add Initialize
    eye1.addInitialize(rad);
    eye2.addInitialize(rad); 
    eye1bg.addInitialize(radbg);
    eye2bg.addInitialize(radbg);
    eye1.addInitialize(life);
    eye2.addInitialize(life);
    eye1bg.addInitialize(lifeBg);
    eye2bg.addInitialize(lifeBg);
    eye1.addInitialize(vel);
    eye2.addInitialize(vel);
    eye1bg.addInitialize(velbg);
    eye2bg.addInitialize(velbg);
    eye1.addInitialize(body);
    eye2.addInitialize(body);

    //add Behaviour
    eye1bg.addBehaviour(new Proton.Color('aaaaff','ccccff'));
    eye2bg.addBehaviour(new Proton.Color('aaaaff','ccccff'));
    eye1bg.addBehaviour(new Proton.Alpha(0.5, 0, NaN, Proton.ease.easeInQuart));
    eye2bg.addBehaviour(new Proton.Alpha(0.5, 0, NaN, Proton.ease.easeInQuart));
    eye1.addBehaviour(new Proton.Gravity(5 * this.scale));
    eye2.addBehaviour(new Proton.Gravity(5 * this.scale));
    eye1.addBehaviour(new Proton.Alpha(1, 0, NaN, Proton.ease.easeInExpo));
    eye2.addBehaviour(new Proton.Alpha(1, 0, NaN, Proton.ease.easeInExpo));
    eye1bg.addBehaviour(new Proton.Gravity(5 * this.scale));
    eye2bg.addBehaviour(new Proton.Gravity(5 * this.scale));
    eye1.addBehaviour(new Proton.Scale(0.2 * this.scale, 2 * this.scale));
    eye2.addBehaviour(new Proton.Scale(0.2 * this.scale, 2 * this.scale));
    eye1bg.addBehaviour(new Proton.Scale(0.7, 3));
    eye2bg.addBehaviour(new Proton.Scale(0.7, 3));
    eye1.addBehaviour(new Proton.Rotate(Proton.getSpan(-15, 15), 0));
    eye2.addBehaviour(new Proton.Rotate(Proton.getSpan(-15, 15), 0));

    //add emitter to the proton
    proton.addEmitter(eye1);
    proton.addEmitter(eye2);
    protonTrail.addEmitter(eye1bg);
    protonTrail.addEmitter(eye2bg);

    eye1.emit();
    eye2.emit();
    eye1bg.emit();
    eye2bg.emit();

    // separate canvas for background with trails
    const trailRenderer = new Proton.CanvasRenderer(this.$refs.trailCanvas);
    trailRenderer.onProtonUpdate = () => { this.alphaFadeCanvas() }
    protonTrail.addRenderer(trailRenderer);

    // fg renderer
    const renderer = new Proton.CanvasRenderer(this.$refs.canvas);
    proton.addRenderer(renderer);

    // begin render
    this.render();
  },
  methods: {
    render() {
      window.requestAnimationFrame(this.render)
      this.resizeCanvasToDisplaySize()

      if(this.playing && this.face.e1 && this.face.e2) {
        
        //set emission rate
        this.eye1.rate = this.rate1
        this.eye1bg.rate = this.rate1bg
        this.eye2.rate = this.rate2
        this.eye2bg.rate = this.rate2bg

        //set emitter position
        this.eye1.p.x = this.eye1bg.p.x = this.face.e1.x * this.scale;
        this.eye1.p.y = this.eye1bg.p.y = this.face.e1.y * this.scale;
        this.eye2.p.x = this.eye2bg.p.x = this.face.e2.x * this.scale;
        this.eye2.p.y = this.eye2bg.p.y = this.face.e2.y * this.scale;
      }
      else {
        // stop emission
        this.eye1.rate = new Proton.Rate(0)
        this.eye1bg.rate = new Proton.Rate(0)
        this.eye2.rate = new Proton.Rate(0)
        this.eye2bg.rate = new Proton.Rate(0)
      }

      //this.alphaFadeCanvas()
      this.proton.update()
      this.protonTrail.update()
    },
    alphaFadeCanvas() {
      const trailCanvas = this.$refs.trailCanvas;
      const trailContext = trailCanvas.getContext("2d");    
      
      trailContext.globalAlpha=0.7;
      trailContext.globalCompositeOperation='copy';
      trailContext.drawImage(trailCanvas,0,0,trailCanvas.width,trailCanvas.height,0,0,trailCanvas.width,trailCanvas.height);
      trailContext.globalAlpha=1.00;
      trailContext.globalCompositeOperation='source-over';
    },
    resizeCanvasToDisplaySize() {

      // look up the size the canvas is being displayed
      const canvas = this.$refs.canvas;
      const trailCanvas = this.$refs.trailCanvas;
      const width = canvas.clientWidth;
      const height = canvas.clientHeight;

      // If its resolution does not match change it
      if (canvas.width !== width || canvas.height !== height) {
        canvas.width = trailCanvas.width = width;
        canvas.height = trailCanvas.height = height;
        this.scale = width / 720;
        return true;
      }

      return false;
    },
    setRate(e, r) {
      e.rate = new Proton.rate(r); 
    },
    // checks if we tapped an interactive particle
    tapCheck(e) {
      // get all interactive particles
      let interactive = this.eye1.particles.concat(this.eye2.particles);
      let vec = new Proton.Vector2D();
      let mouseP = {x:e.offsetX, y:e.offsetY}
      // loop through particles to hit test
      for(let i = 0; i < interactive.length; i++) {
        let p = interactive[i];
        // get tap distance from particle
        vec.subVectors(p.p, mouseP);
        // is distance smaller than particle radius?
        if(vec.length() < p.body.width / 2 * p.scale * 1.5) {
          // pop emoji
          this.popEmoji(p);
          // only pop 1 emoji per tap
          break;
        }
      }
    },
    popEmoji(particle) {

      // create new emitter for explosion
      const popEmitter = new Proton.Emitter();
      popEmitter.rate = new Proton.Rate(new Proton.Span(50, 60), 1);
      popEmitter.addInitialize(new Proton.Mass(1));
      popEmitter.addInitialize(new Proton.Radius(6 * this.scale, 8 * this.scale));
      popEmitter.addInitialize(new Proton.Life(0.5));
      popEmitter.addInitialize(
        new Proton.V(new Proton.Span(0, 3), new Proton.Span(0, 360), "polar")
      );

      popEmitter.addBehaviour(new Proton.RandomDrift(10, 10, 0.05));
      popEmitter.addBehaviour(new Proton.Alpha(1, 0));
      popEmitter.addBehaviour(new Proton.Gravity(5 * this.scale));
      popEmitter.addBehaviour(new Proton.Color('ffff66','ffffaa'));
      popEmitter.addBehaviour(new Proton.Scale(1.5, 0.4));

      popEmitter.p.x = particle.p.x;
      popEmitter.p.y = particle.p.y;
      popEmitter.emit("once", true);

      this.protonTrail.addEmitter(popEmitter);

      // kill the particle
      particle.removeAllBehaviours();
      particle.destroy();


      // make sound
      this.$refs.popSound.currentTime = 0;
      this.$refs.popSound.play();

      // emit event 
      this.$emit('pop');
    }
  }
}
</script>

<style scoped>
div.container, canvas {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0; top: 0;
}
.trail-canvas {
  pointer-events: none;
}
</style>